//计数器
var interval = null;
//值越大旋转时间越长  即旋转速度
var intime = 50;
export default {
	data() {
		return {
			color: [{
					opa: 0.7,
					name: "红包"
				},
				{
					opa: 0.7,
					name: "10Q币"
				},
				{
					opa: 0.7,
					name: "红包"
				},
				{
					opa: 0.7,
					name: "京东卡"
				},
				{
					opa: 0.7,
					name: "京东卡"
				},
				{
					opa: 0.7,
					name: "谢谢参与 "
				},
				{
					opa: 0.7,
					name: "10Q币"
				},
				{
					opa: 0.7,
					name: "红包"
				}
			],
			btnconfirm: '../../static/award-btn.gif',
			clickLuck: 'clickLuck',
			luckPosition: 1,
		}
	},
	methods: {
		input: function(e) {
			var data = Number(e.detail.value);
			this.luckPosition = data;
		},
		//点击抽奖按钮
		clickLucks: function() {
			if (this.clickLuck == 'clickLuck') {
				var e = this;
				//判断中奖位置格式
				if (e.luckPosition == null || isNaN(e.luckPosition) || e.luckPosition > 7) {
					uni.showModal({
						title: '提示',
						content: '请填写正确数值',
						showCancel: false,
						success: res => {},
						fail: () => {},
						complete: () => {}
					});
					return;
				}

				//设置按钮不可点击
				e.btnconfirm = '../../static/award-btn.gif';
				e.clickLuck = '';

				//清空计时器
				clearInterval(interval);
				var index = 0;
				//循环设置每一项的透明度
				interval = setInterval(function() {
					if (index > 7) {
						index = 0;
						e.color[7].opa = 0.7
					} else if (index != 0) {
						e.color[index - 1].opa = 0.7
					}
					e.color[index].opa = 1;
					index++;
				}, intime);
				//模拟网络请求时间  设为两秒
				var stoptime = 10;
				setTimeout(function() {
					e.stop(e.luckPosition);
				}, stoptime)
			}
		},
		stop: function(which) {
			var e = this;
			//清空计数器
			clearInterval(interval);
			//初始化当前位置
			var current = -1;
			var color = e.color;
			for (var i = 0; i < color.length; i++) {
				if (color[i].opa == 1) {
					current = i;
				}
			}
			//下标从1开始
			var index = current + 1;
			e.stopLuck(which, index, intime, 10);
		},
		stopLuck: function(which, index, time, splittime) {
			var e = this;
			//值越大出现中奖结果后减速时间越长
			setTimeout(function() {
				//重置前一个位置
				if (index > 7) {
					index = 0;
					e.color[7].opa = 0.7
				} else if (index != 0) {
					e.color[index - 1].opa = 0.7
				}
				//当前位置为选中状态
				e.color[index].opa = 1;
				//如果旋转时间过短或者当前位置不等于中奖位置则递归执行
				//直到旋转至中奖位置
				if (time < 400 || index != which) {
					//越来越慢
					splittime++;
					time += splittime;
					//当前位置+1
					index++;
					e.stopLuck(which, index, time, splittime);
				} else {
					//1秒后显示弹窗
					setTimeout(function() {
						if (which == 0) {
							//中奖
							uni.showModal({
								content: '恭喜获得：' + e.color[which].name,
								showCancel: false,
								confirmColor: "#F8C219",
								success: res => {
									if (res.confirm) {
										//设置按钮可以点击
										e.btnconfirm = '../../static/award-btn.gif';
										e.clickLuck = 'clickLuck';
										e.loadAnimation();
									}
								},
								fail: () => {},
								complete: () => {}
							});
						} else {
							//未中奖
							uni.showModal({
								content: '很遗憾未中奖',
								showCancel: false,
								confirmColor: "#F8C219",
								success: res => {
									if (res.confirm) {
										//设置按钮可以点击
										e.btnconfirm = '../../static/award-btn.gif';
										e.clickLuck = 'clickLuck';
										e.loadAnimation();
									}
								},
								fail: () => {},
								complete: () => {}
							});
						}
					}, 1000);
				}
			}, time);
		},
		loadAnimation: function() {
			var e = this;
			var index = 0;
			if (interval == null) {
				interval = setInterval(function() {
					if (index > 7) {
						index = 0;
						e.color[7].opa = 0.7
					} else if (index != 0) {
						e.color[index - 1].opa = 0.7
					}
					e.color[index].opa = 1;
					index++;
				}, 1000);
			}
		}
	},
	onLoad() {
		uni.hideLoading()
		this.loadAnimation();
	}
}
